import React, { Component } from 'react';
import AddNote from './Note/AddNote.js'
import Header from './Note/Header.js'
import Notes from './Note/Notes.js'
import firebase from 'firebase'
import {dataNote} from './../data/ConnFirebase.js'

export default class Note extends Component {
	constructor(props){
		super(props);
		this.state ={
			data:[]
		}
	}
	componentWillMount(){
		dataNote.on('value',(notes) =>{
			var dataArr=[];
			notes.forEach(elem => {
				const key = elem.key;
				const title=elem.val().title;
				const content=elem.val().content;
				dataArr.unshift({
					id:key,
					title:title,
					content:content
				});
			});
			this.setState({
				data:dataArr
			})
		})
	}
	showNote = ()=>(

		this.state.data.map((value)=>{
			return (<Notes title={value.title} content={value.content} 
					datatarget={value.id} id={value.id}/>)
			
		})
	);
		
	
	pushNote = (item) => {
	    var connData = firebase.database().ref('data/');
	    connData.push({
	      title:item.title,
	      content:item.content
	    });
	 
	}
	
	render() {
		return (
			<div className="mt-5">
				<Header/>
				<div className="container">
					<div className="row">
						<div className="col-6 btn-default ">
							{this.showNote()}
						</div>
						<div className="col-6 btn-default">
							<AddNote getNote={(item)=>this.pushNote(item)}/>
						</div>
					</div>
				</div>
			</div>

		);
	}
}
