import React, { Component } from 'react';

export default class AddUser extends Component {
	constructor(props){
		super(props);
		this.state = {
			// status: false
			id:'',
			name:'',
			number: '',
			permission:''
		}
	}
	isChange =(event)=>{
		const name = event.target.name;
		const value = event.target.value;
		this.setState({
			[name]:value
		})
		
		
	}
	
	DisplayForm = () => {
		if(this.props.ChangingStatus===true){
			return (
			<div className="col">
				<div className="card text-left">
		          <div className="card btn-default mb-3" style={{maxWidth: '18rem'}}>
		            <div className="card-body">
		              <form method="post">
		                <div className="form-group">
		                  <label >Name</label>
		                  <input type="text" name="name" onChange={(event)=>this.isChange(event)} className="form-control"  aria-describedby="emailHelp" placeholder="Enter Name" required />
		                </div>
		                <div className="form-group">
		                  <label >Number Phone</label>
		                  <input type="text" name="number" onChange={(event)=>this.isChange(event)} className="form-control"  placeholder="Enter Phone Number" required />
		                </div>
		                <div className="form-group">
		                  <label >User Permission</label>
		                  <select name = "permission" onChange={(event)=>this.isChange(event)} className="custom-select" required>
		                    <option value={1}>Admin</option>
		                    <option value={2}>Super User</option>
		                    <option value={3}>User</option>
		                  </select>
		                </div>
		                <input type="reset" onClick={(name,number,permission)=>this.props.add(this.state.name, this.state.number,this.state.permission)} className="btn btn-success" value="Add new user"/>
		              </form>
		              
		            </div>
		          </div>
		        </div>
			</div>
				
	        );
	    }
	}

	render() {
		
		return (
		
			<div>
        		{this.DisplayForm()}
			</div>
        	
      	
		);
	}
}
