import React, { Component } from 'react';
export default class Table extends Component {
  constructor(props){
    super(props);
    this.state = {

    }
  }
   showPermission = () =>{
      if(this.props.permission === "1"){
        return "Admin";
      }
      else if(this.props.permission ==="2"){
        return "Super User"
      }else{
        return "User"
      }
    }
    editClick = (item) => {
      this.props.editFunc(item);
      this.props.StatusEdit();
    }
    deleteButClick = (idUser) =>{
     this.props.deleteButClick(idUser);
    }
	render() {
   
		return (
             <tr>
              <td>{this.props.id}</td>
              <td>{this.props.name}</td>
              <td>{this.props.number}</td>
              <td>{this.showPermission()}</td>
              <td>
                <div className="btn-block">
                  <button onClick={(item)=>this.editClick(item)}className="btn btn-primary ">Edit</button>
                  <button onClick={(idUser)=>this.deleteButClick(this.props.id)} className="btn btn-danger ">Delete</button>
                </div>
                
              </td>
            </tr>
          
        
  
			
		);
	}
}
