import React, { Component } from 'react';

export default class EditUser extends Component {
	constructor(props){
		super(props);
		this.state = {
			id:this.props.userEditObj.id,
			name:this.props.userEditObj.name,
			number: this.props.userEditObj.number,
			permission:this.props.userEditObj.permission
		}
	}
	isChange =(event)=>{
		const name = event.target.name;
		const value = event.target.value;
		this.setState({
			[name]:value
		})
	}
	reOnClick = () =>{
		var info={};
		info.id=this.state.id;
		info.name=this.state.name;
		info.number=this.state.number;
		info.permission=this.state.permission;
		
		this.props.getUserInfo(info);
		this.props.StatusEdit();
	}
	render() {
		return (
			<div className="v">
				<div className="card text-left">
		          <div className="card btn-default mb-3" style={{maxWidth: '18rem'}}>
		            <div className="card-body">
		              <form method="post">
		                <div className="form-group">
		                  <label >Name</label>
		                  <input onChange={(event)=>this.isChange(event)} defaultValue={this.props.userEditObj.name} type="text" name="name"  className="form-control"  placeholder="Enter Name" required />
		                </div>
		                <div className="form-group">
		                  <label >Number Phone</label>
		                  <input onChange={(event)=>this.isChange(event)} defaultValue={this.props.userEditObj.number} type="text" name="number" className="form-control"  placeholder="Enter Phone Number" required />
		                </div>
		                <div className="form-group">
		                  <label >User Permission</label>
		                  <select onChange={(event)=>this.isChange(event)} defaultValue={this.props.userEditObj.permission} name = "permission" className="custom-select" required>
		                    <option value={1}>Admin</option>
		                    <option value={2}>Super User</option>
		                    <option value={3}>User</option>
		                  </select>
		                </div>
		                <button type="submit" onClick={()=>this.reOnClick()} className="btn btn-success" >Save</button>
		              </form>
		              
		            </div>
		          </div>
		        </div>
			</div>
			
		);
	}
}
