import React, { Component } from 'react';
import TableData from './TableData.js'
export default class Table extends Component {
	constructor(props){
		super(props);
		this.state={

		}
	}
	deleteButClick = (idUser) =>{
		this.props.deleteUser(idUser);
	}
	ShowTableData = () => (
		this.props.dataProps.map((value) =>(
			<TableData deleteButClick={(idUser)=>this.deleteButClick(idUser)} editFunc={(item)=>this.props.editUser(value)} id={value.id} name={value.name} 
			number={value.number} permission={value.permission} StatusEdit={()=>this.props.StatusEdit()}/>
		))
	)

	render() {
		
		return (
			<div className="col text-center">
		        <table className="table table-triped table-hover table-bordered ">
		          <thead>
		            <tr>
		              <th>STT</th>
		              <th>Name</th>
		              <th>PhoneNumber </th>
		              <th>Permission</th>
		              <th>Edit</th>
		            </tr>
		         </thead>
		          <tbody>
		             {this.ShowTableData()}
		          </tbody>
		        </table>
		     </div>
		);
	}
}
