import React, { Component } from 'react';
import { Editor } from '@tinymce/tinymce-react'; 


class AddNote extends Component {
	constructor(props){
		super(props);
		this.state = {
			title:'',
			content:''
		}
	}
	handleEditorChange = (event) => {
		this.setState({
			
			content:event.target.getContent()
		})
    console.log(
      'Content was updated:',
      event.target.getContent()
    );
  }
	isChange = (event) => {
		
		const name = event.target.name;
		const value = event.target.value;
		this.setState({
			[name]:value,
			
		})
		
	}
	addData = (title,content) => {
		var item = {};
		item.title = title;
		item.content= content;
		this.props.getNote(item);
		
	}
	render() {
		console.log(this.state.content);
		return (
		<div className="card text-left">
	          <div className="card btn-default mb-3">
	            <div className="card-body">
	              <form >
	                <div className="form-group">
	                  <label htmlFor="title">Title</label>
	                  <input onChange={(event)=>this.isChange(event)} type="text" 
	                  name="title" className="form-control" id="title" placeholder="Note Title " required />
	                </div>
	                <div className="form-group">
	                <Editor initialValue="Initial content"
	                	init={{
						          height: 500,
						          menubar: false,
						          plugins: [
				            'advlist autolink lists link image', 
				            'charmap print preview anchor help',
				            'searchreplace visualblocks code',
				            'insertdatetime media table paste wordcount'
				          ],
				          toolbar:
				            'undo redo | formatselect | bold italic | \
				            alignleft aligncenter alignright | \
				            bullist numlist outdent indent | help'
				        }}
				          onChange={this.handleEditorChange} />
				        </div>
	                <input onClick={()=>this.addData(this.state.title, this.state.content)} 
	                type="reset" className="btn btn-success btn-block" value="Add" />
	              </form>
	            </div>
	        </div>
	       </div>
       
     
		);
	}
}
