import React, { Component } from 'react';
import Header from './Manage_Users/Header.js'
import SearchTab from './Manage_Users/SearchTab.js'
import Table from './Manage_Users/Table.js'
import AddUser from './Manage_Users/AddUser.js'
import DataUser from './Manage_Users/Data.json'


const { v4: uuidv4 } = require('uuid');


class UsersApp extends Component {
  constructor(props){
    super(props);
    this.state = {
      status: false,
      data  : DataUser,
      searchText:'',
      changeStatusEdit:false,
      userEditObj:{}
    }
  }
  componentWillMount(){
    if (localStorage.getItem('userData')===null) {
      localStorage.setItem('userData',JSON.stringify(DataUser));
    }else{
      var temp = JSON.parse(localStorage.getItem('userData'));
      this.setState({
        data:temp
      })
    }
  }
  ChangingStatus = () => {
    this.setState({
      status:!(this.state.status)
    })  
  }
  getTextSearch = (dl) =>(
     this.setState({
       searchText : dl
     })
    );
  StatusEdit = () =>{
    this.setState({
      changeStatusEdit:!(this.state.changeStatusEdit)
    });
  }
  editUser = (item) => {
     this.setState({
       userEditObj:item
     })
    
  }
  getNewUser = (name,number,permission) =>{
    var user={};
    user.id=uuidv4();
    user.name=name;
    user.number = number;
    user.permission = permission;
    var users=this.state.data;
    users.push(user);
    this.setState({
      data:users
    })
    localStorage.setItem('userData', JSON.stringify(users));
  }
  getUserInfoApp =(info)=>{
    this.state.data.forEach((value,key)=>{
      if(value.id===info.id){
        value.name=info.name;
        value.number=info.number;
        value.permission=info.permission;
    }
  });
   localStorage.setItem('userData', JSON.stringify(this.state.data));
  }

    deleteUser = (idUser) => {
      var tempData=this.state.data.filter((item)=>(item.id!==idUser));
      this.setState({
        data:tempData
      });
      localStorage.setItem('userData', JSON.stringify(tempData));
    }
  render() {
    // console.log(DataUser);
    let resultSearch = [];
    this.state.data.forEach((item)=>{
      if(item.name.indexOf(this.state.searchText)>-1){
        resultSearch.push(item);
      }
    })
    return (
        <div>
          <Header/>
          <div className="container">
            
            <SearchTab getUserInfoApp={(info)=>this.getUserInfoApp(info)} userEditObj={this.state.userEditObj} changeStatusEdit={this.state.changeStatusEdit} StatusEdit={()=>this.StatusEdit()} connection={()=>{this.ChangingStatus()}} 
            ChangingStatus={this.state.status} connectSearch={(dl)=>this.getTextSearch(dl)}/>
            <div className="row">
                <Table deleteUser={(idUser)=>this.deleteUser(idUser)} StatusEdit={()=>this.StatusEdit()} editUser={(item)=>this.editUser(item)} dataProps={resultSearch} />
                <AddUser add={(name,number,permission)=>this.getNewUser(name,number,permission)} ChangingStatus={this.state.status}/>
            </div>
          </div>
        </div>
  );
  }
}


export default UsersApp;
