import React, { Component } from 'react';
import NewItem from './NewItem.js'
import dl from './Data.json'


class Home extends Component {

	render() {
     
		return (
      <div>
        <div className="container"   style={{marginTop: '90px'}}>
          <div className="row">
           {
             dl.map((value, key)=> {
               return (<NewItem anh={value.anh} tieude={value.tieude}
               link={value.link} noidung={value.noidung} />);
             })
           }
          
            
         </div>
        </div>
        
      </div>
			
		);
	}
}
export default Home;